.login-form
{
    width: 80vh;
    min-height: 250px;
    border-radius: 6px 6px 6px 6px;
    -webkit-box-shadow: 0 8px 6px -6px rgba(0,0,0,0.8);
            box-shadow: 0 8px 6px -6px rgba(0,0,0,0.8);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack:start;
        justify-content:flex-start;
    -ms-flex-align: center;
        align-items: center;
    background: whitesmoke;
}

.login-message-div
{
    width: 80%;
    margin-top: 20px;
    color: red;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack:start;
        justify-content:flex-start;
}

.login-username-div
{
    width: 80%;
    margin-top: 10px;
    padding: 10px 5px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack:start;
        justify-content:flex-start;
}

.login-password-div
{
    width: 80%;
    margin: 10px 0;
    padding: 10px 5px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack:start;
        justify-content:flex-start;
}

.login-button-div
{
    width: 80%;
    margin-bottom: 20px;
    padding: 10px 5px;
}

.login-label
{
    display: inline-block;
    font-weight: 400;
    padding-bottom: 3px;
}

.login-input
{
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.login-submit-button
{
    width: 100%;
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    padding: 14px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
.login-submit-button:hover
{
    background-color: #45a049;
}




