.dashboard-root-div
{
    width:100%;
    min-height:100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: whitesmoke;
}

.dashboard-row-div
{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    border-radius: 4px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    border: 1px solid #ccc;
    /*background: whitesmoke;*/
}

.dashboard-image-div
{
    margin: 20px 50px;
    padding: 20px;
    background: #ccc;
    border: 1px solid #ccc;
    border-radius: 8px;
    -webkit-box-shadow: 0 8px 6px -6px rgba(0,0,0,0.8);
            box-shadow: 0 8px 6px -6px rgba(0,0,0,0.8);
    cursor: pointer;
}


